.container {
  display: flex;
  background-color: rgb(255, 255, 255);
  border-radius: 15vw;
  padding: 2px;
  margin-top: 60px;
  text-align: center;
  font-family: 'GmarketSansMedium';
}

.wrapper {
  margin: 100px 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'GmarketSansMedium';
}

.title {
  width: 80%;
  font-weight: 500;
  font-size: 13px;
  margin-top: 20px;
  text-align: center;
  font-family: 'Happiness-Sans-Regular';
}

a {
  text-decoration: none;
  color: black;
}
a:visited {
  color: black;
}

.btn:last-child {
  margin-right: 0;
}
.section {
  display: flex;
  flex-direction: column;
}

.sectionTop {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 1px solid rgba(128, 128, 128, 0.247);
}

.sectionTop h2 {
  font-size: 18px;
  font-weight: 500;
  margin-top: 20px;
  margin-bottom: 10px;
}

.sectionTop span {
  font-size: 11px;
  margin-bottom: 10px;
}
.coverImg {
  width: 130px;
  border-radius: 5%;
}

.sectionBottom {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.sectionBottom div {
  font-size: 13px;
  width: 90%;
  text-align: center;
  line-height: 20px;
}

.writeForm {
  width: 70vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.writeForm div {
  font-family: 'GmarketSansMedium';
}
.writeForm div label {
  margin-bottom: 5px;
}

.writeForm div input {
  width: 100%;
  border: none;
  outline: none;
  border-bottom: 1px solid #696969;
  padding: 5px;
  width: 55vw;
}

.writeForm div textarea {
  height: 20vh;
  padding: 0;
  outline: none;
  font-size: 1.4rem;
  width: 100%;
  font-family: 'GmarketSansMedium';
}

.title {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.Btn {
  background-color: black;
  border: none;
  color: white;
  padding: 10px 80px;
  margin-top: 30px;
  cursor: pointer;

  border-radius: 5px;
  box-shadow: 0px 0px 1px 1px rgb(0, 0, 0);
  font-family: 'GmarketSansMedium';
}

.Btn:hover {
  color: black;
  background-color: skyblue;
}

.recommendSection {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 15px;
}

.WriteContainer {
  height: 100vh;
  margin-top: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.writeTop {
  display: flex;
  justify-content: center;
  align-items: center;
}

.writeTop img {
  width: 80px;
}

.writeTop section {
  margin-left: 15px;
  margin-top: 5px;
  font-family: 'GmarketSansMedium';
}

.writeTop section h1 {
  font-size: 20px;

  margin-bottom: 20px;
  margin-top: 0;
}

.writeTop section span {
  display: block;
  margin: 5px 0;
  font-size: 0.8rem;
  font-weight: 400;
}

.review {
  width: 70vw;
}
