.wrapper {
  margin: 100px 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'GmarketSansMedium';
}

.section {
  display: flex;
  flex-direction: column;
}

.sectionTop {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 1px solid rgba(128, 128, 128, 0.247);
}

.sectionTop h2 {
  font-size: 18px;
  font-weight: 500;
  margin-top: 20px;
  margin-bottom: 10px;
}

.sectionTop span {
  font-size: 11px;
  margin-bottom: 10px;
}
.coverImg {
  width: 30vw;
  border-radius: 5%;
}

.sectionBottom {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.sectionBottom div {
  font-size: 13px;
  width: 90%;
  text-align: center;
  line-height: 20px;
}

.writeBtn {
  background-color: black;
  width: 80%;
  height: 100%;
  border: none;
  color: white;
  padding: 10px 30px;
  margin-top: 30px;
  cursor: pointer;
  border-radius: 5px;
  box-shadow: 0px 0px 1px 1px rgb(0, 0, 0);
  font-family: 'GmarketSansMedium';
}

.writeBtn:hover {
  color: black;
  background-color: skyblue;
}
